import React from "react"

const Project = ( props ) => {
  return(
    <div>
      <div>
        <a href={ props.link }><h3>{ props.title }</h3></a>
        <p>{ props.description }</p>
        <ul>
          { props.bullets.map( (element, i) => <li key={ i }>{ element }</li> ) }
        </ul>
      </div>
      <img src={ props.imgSrc } />
    </div>
    
  )
}

